import instance from "../../api/index.js"
export default{
	data(){
		return {
				form:{
					tel: '',
					password: '',
				}
		}
	},
	methods:{
		
		toindex(){
			uni.switchTab({
				url:"../index/index"
			})
		},
		toreginst(){
			uni.navigateTo({
				url:"../register/register"
			})
		},
		submit(){
				// 表单校验
				let loginRules = [
					{name: 'tel', required: true, type: 'phone', errmsg: '请输入正确的手机号'},
					{name: 'password', type: 'required', errmsg: '请输入密码'},
					{name: 'password', type: 'pwd', errmsg: '密码须是6～16位字符'}
				]
				let valLoginRes = this.$validate.validate(this.form, loginRules)
				
				if (!valLoginRes.isOk) {
					uni.showToast({
						icon: 'none',
						title: valLoginRes.errmsg
					})
					return false
				}else{
					instance({
							url:"/api/member/appLogin",
							data:{
								mobileNo:this.form.tel,
								password:this.form.password,
								bindNo:"17570710275"
							},
							method:'POST',
							header:{
							   'content-type':'application/json',
							}
					}).then(res=>{
						uni.showToast({
							icon: 'none',
							title: '登录成功'
						})
						uni.setStorageSync("token",res.data.data.token)
						console.log(res)
						uni.switchTab({
							url:"../center/center"
						})
					})
				}
				
				
		
		}
	}
}
